import MenuItem from "./menu-item";
export default function Menu({ items = [], value = "", onChange }) {
  return (
    <ul className="flex gap-2 text-xs font-bold pl-6">
      {items?.map((item) => (
        <MenuItem
          key={item.id}
          isActive={item.id == value}
          label={item.label}
          onClick={() => onChange && onChange(item.id)}
        />
      ))}
    </ul>
  );
}
