import { useState } from "react";
import Menu from "./menu";

const MENU = [
  { id: "home", label: "Home" },
  { id: "formulas", label: "Formulas" },
  { id: "data", label: "Data" },
  { id: "view", label: "View" },
  { id: "about", label: "About" },
];

export default function Header() {
  const [activeMenu, setActiveMenu] = useState(MENU[0].id);
  return (
    <header className="shadow-inner px-2 pt-2 bg-teal-900">
      <Menu items={MENU} value={activeMenu} onChange={setActiveMenu} />
    </header>
  );
}
